










import { Component, Vue, Prop } from "vue-property-decorator";
import { Lang } from "@/control/Lang";

@Component
export default class SetupLang extends Vue {
  @Prop({ type: String, required: true })
  value!: string;

  input(lang: Lang) {
    this.$emit("input", lang);
  }
  langs: Lang[] = ["ru", "en", "de"];
}
