














import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class Video extends Vue {
  @Prop({ type: String, required: true })
  name!: string;
  videoEl!: HTMLVideoElement;
  subsHidden = true;
  primarySubsShowed = false;
  nativeSubsShowed = false;
  timestamp: number = 0;
  path!: string;

  created() {
    this.path = `${process.env.BASE_URL}content/${this.$props.name}`;
  }

  mounted() {
    this.videoEl = <HTMLVideoElement>this.$el;
  }

  seeked(_e: Event): void {
    if (this.timestamp <= this.videoEl.currentTime) {
      return;
    }
    if (this.primarySubsShowed) {
      // @ts-ignore
      this.videoEl.textTracks.getTrackById("nativeSubs").mode = "showing";
      return;
    }
    // @ts-ignore
    this.videoEl.textTracks.getTrackById("primarySubs").mode = "showing";
    this.primarySubsShowed = true;
    this.subsHidden = false;
  }

  timeupdate() {
    if (this.timestamp > this.videoEl.currentTime) {
      return;
    }
    !this.subsHidden && this.hideSubs();
    this.timestamp = this.videoEl.currentTime;
  }

  hideSubs() {
    this.subsHidden = true;
    this.primarySubsShowed = false;
    this.nativeSubsShowed = false;
    // @ts-ignore
    this.videoEl.textTracks.getTrackById("nativeSubs").mode = "hidden";
    // @ts-ignore
    this.videoEl.textTracks.getTrackById("primarySubs").mode = "hidden";
  }
}
